import React, { Component } from "react";
import "./error.scss";
import { connect } from "react-redux";
import uploadIcon from "../../assets/images/uploadIcon.png";
import * as Actions from "../../store/User/UserAction";
import { withRouter } from "react-router-dom";
import { bindActionCreators } from "redux";
import { store } from "react-notifications-component";
import errorImage from "../../assets/images/errorImage.svg";
export class Submitting extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }
  componentDidMount() {}

  static getDerivedStateFromProps(nextProps, currentState) {
    return currentState;
  }

  render() {
    return (
      <div className="error-main">
        <div className="error-image-div">
          <div className="image"></div>
          <img src={errorImage} />
        </div>
        {/* <div className="error-text">
          Error submitting response, please check Internet connection, otherwise
          contact HR!
        </div> */}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    UserReducer: state.UserReducer,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Submitting));
